export default Page;

export const query = graphql`
  query {
    recruits: allFile(
      filter: { relativePath: { regex: "/^recruit-juice-/" } }
      sort: { relativePath: ASC }
    ) {
      edges {
        node {
          childImageSharp {
            gatsbyImageData(width: 1920, placeholder: BLURRED)
          }
        }
      }
    }
    voices: allFile(
      filter: { relativePath: { regex: "/^voice-/" } }
      sort: { relativePath: ASC }
    ) {
      edges {
        node {
          childImageSharp {
            gatsbyImageData(width: 240, height: 240, placeholder: BLURRED)
          }
        }
      }
    }
  }
`;

export const Head = () => (
  <Seo title='自由な働き方で高収入を目指せる！オレンジジュース自販機補充スタッフ募集' />
);
